.container-slider {
  width: 100%;
  height: 420px;
}

.slide {
  width:0px;
  height:0px;
  margin:0px;
  opacity: 0;
  transition: opacity ease-in-out 0.4s;
}
.slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 5px;
}

.slide figcaption {
  font-size: 18px;
	margin-top:-2rem;
	text-align:center;
	letter-spacing: 0.05em;
	word-spacing: 0.05em;
	color: white;
}

.slide.active-anim {
  opacity: 1;
  width: 100%;
  height: 420px;
}

.btn-slide {
  background: none;
  border: none;
  position: absolute;
  cursor: pointer;
}

.btn-slide i {
  color: white;
  font-size: 60px;
}
.prev {
  top: 25em;
  left: 70px;
}
.next {
  top: 25em;
  right: 50px;
}

@media (max-width: 767px) {

  .container-slider {
    width: 100%;
    height: 255px;
  }
  .slide.active-anim  {
    width: 100%;
    height: 255px;
  }
  .prev {
    top:15em;
    left: 35px;
  }
  .next {
    top:15em;
    right: 25px;
  }

  .slide figcaption {
    margin-top:-2rem;
  }

}
